import ToggleCartIcon from "../components/ToggleCartIcon";
import { useLocation, useParams } from "react-router-dom";
import queryString from "query-string";
import { useState } from "react";

const Menu = ({ products, categories, hadleAddToCart }) => {
  const [message, setMessage] = useState("");

  const handleSearch = (event) => {
    setMessage(event.target.value);
    var search = event.target.value;
  };
  const searchedProducts = products.filter((p) => p.name.toLowerCase().includes(message.toLowerCase()));

  //   --------- Filter ---------
  const [currentCategory, setCurrentCategory] = useState(1);

  // Get the filterd item
  const filteredItems = currentCategory === 0 ? products : products.filter((p) => p.category === currentCategory);

  //   --------- Pagination ---------
  const itemsCountPerPage = 4;
  const [currentPage, setCurrentPage] = useState(1);

  //   Get the start index in the page
  const startIndexInPage = (currentPage - 1) * itemsCountPerPage;

  //   Get the items in the page
  const paginatdItems = filteredItems.slice(startIndexInPage, startIndexInPage + itemsCountPerPage);

  //   Get no of pages
  const noOfPages = Math.ceil(filteredItems.length / itemsCountPerPage);

  //   Get array of pages numbers
  let pagesArray = [];
  for (let i = 1; i <= noOfPages; i++) {
    pagesArray.push(i);
  }

  //   Change Page handler
  const handleChangePage = (page) => {
    setCurrentPage(page);
  };

  //   change the current cutegory handler
  const handleChangeCategory = (categryId) => {
    setCurrentCategory(categryId);
    setCurrentPage(1);
  };

  return (
    <div className="row mt-3">
      <div className="col-3">
        <ul className="list-group">
          {categories.map((cat) => (
            <li
              style={{ cursor: "pointer" }}
              onClick={() => handleChangeCategory(cat.id)}
              key={cat.id}
              className={cat.id === currentCategory ? "list-group-item active" : "list-group-item"}
            >
              {cat.name}
            </li>
          ))}
        </ul>
      </div>
      <div className="col-9">
        {currentCategory == 4 && (
          <>
            <div class="mb-3 w-50">
              <h5 for="inputLarge">Search all items :</h5>
              <input
                class="form-control form-control-lg border-primary "
                type="text"
                placeholder="Enter item..."
                id="inputLarge"
                onChange={handleSearch}
                value={message}
              />
            </div>
            {/* <div className="d-flex justify-content-center align-items-center mb-2">
              <h6 className="mt-2 me-3">Search :</h6>
              <input type="text" id="message" name="message"></input>
            </div> */}
            <div>
              <table className="table">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Price</th>
                    <th>Add to cart</th>
                  </tr>
                </thead>
                <tbody>
                  {searchedProducts.map((p) => (
                    <tr key={p.id}>
                      <td>{p.name}</td>
                      <td>{p.price} $</td>
                      <td style={{ cursor: "pointer" }} onClick={() => hadleAddToCart(p.id)}>
                        <ToggleCartIcon isInCart={p.isInCart} />
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </>
        )}
        {currentCategory != 4 && (
          <table className="table">
            <thead>
              <tr>
                <th>Name</th>
                <th>Price</th>
                <th>Add to cart</th>
              </tr>
            </thead>
            <tbody>
              {paginatdItems.map((p) => (
                <tr key={p.id}>
                  <td>{p.name}</td>
                  <td>{p.price} $</td>
                  <td style={{ cursor: "pointer" }} onClick={() => hadleAddToCart(p.id)}>
                    <ToggleCartIcon isInCart={p.isInCart} />
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}

        {/* Pagination */}
        {pagesArray.length !== 1 && (
          <nav aria-label="...">
            <ul className="pagination pagination-sm">
              {pagesArray.map((page) => (
                <li
                  key={page}
                  style={{ cursor: "pointer" }}
                  className={page === currentPage ? "page-item active" : "page-item"}
                  aria-current="page"
                  onClick={() => handleChangePage(page)}
                >
                  <span className="page-link">{page}</span>
                </li>
              ))}
            </ul>
          </nav>
        )}
      </div>
    </div>
  );
};

export default Menu;
