import React from "react";
import Product from "../components/ProductItem";

const Cart = ({ products, handleIncrement, handleDecrement, handleReset, handleDelete }) => {
  let sum2 = 0;
  const sum = products.map((p) => (sum2 += p.price * p.count));
  return (
    <>
      <h1>Cart items</h1>

      <div className="d-flex flex-column justify-content-center">
        {products.length === 0 && <h3 style={{ color: "grey" }}>Please add items to cart</h3>}
        {products.map((p) => (
          <Product
            key={p.name}
            product={p}
            handleIncrement={handleIncrement}
            handleDecrement={handleDecrement}
            handleDelete={() => handleDelete(p.id)}
          />
        ))}
        <hr className="col-11" />
        <div className="d-flex ">
          <h3 className="me-5">Total</h3>
          <h3>{sum2}$</h3>
          {products.length !== 0 && (
            <div className="d-flex justify-content-center ms-auto col-5">
              <div className="btn btn-secondary ms-2" onClick={handleReset}>
                Reset
              </div>
              <div className="btn btn-primary ms-2">Checkout</div>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default Cart;
