import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { useState } from "react";

import Cart from "./pages/Cart";
import About from "./pages/About";
import Header from "./components/Header";
import Home from "./pages/Home";
import ProductPage from "./pages/ProductPage";
import NotFound from "./pages/NotFound";
import AboutTeam from "./pages/AboutTeam";
import AboutCompany from "./pages/AboutCompany";
import Menu from "./pages/Menu";

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const App = () => {
  const [products, setProducts] = useState([
    {
      id: 1,
      name: "Big Burger",
      img: "/imgs/burger.png",
      price: 50,
      count: 0,
      isInCart: false,
      category: 1,
    },
    {
      id: 2,
      name: "Medium Burger",
      img: "/imgs/burger.png",
      price: 40,
      count: 0,
      isInCart: false,
      category: 1,
    },
    {
      id: 3,
      name: "Small Burger",
      img: "/imgs/burger.png",
      price: 30,
      count: 0,
      isInCart: false,
      category: 1,
    },
    {
      id: 4,
      name: "Big Fries",
      img: "/imgs/fries.png",
      price: 25,
      count: 0,
      isInCart: false,
      category: 2,
    },
    {
      id: 5,
      name: "Medium Fries",
      img: "/imgs/fries.png",
      price: 20,
      count: 0,
      isInCart: false,
      category: 2,
    },
    {
      id: 6,
      name: "Small Fries",
      img: "/imgs/fries.png",
      price: 15,
      count: 0,
      isInCart: false,
      category: 2,
    },
    {
      id: 7,
      name: "Big Cola",
      img: "/imgs/cola.png",
      price: 15,
      count: 0,
      isInCart: false,
      category: 3,
    },
    {
      id: 8,
      name: "Medium Cola",
      img: "/imgs/cola.png",
      price: 10,
      count: 0,
      isInCart: false,
      category: 3,
    },
    {
      id: 9,
      name: "Small Cola",
      img: "/imgs/cola.png",
      price: 7,
      count: 0,
      isInCart: false,
      category: 3,
    },
  ]);

  const categories = [
    { id: 4, name: "search" },
    { id: 0, name: "All" },
    { id: 1, name: "Burger" },
    { id: 2, name: "Fries" },
    { id: 3, name: "Cola" },
  ];

  const [itemsCount, setItemsCount] = useState(0);
  //   Handlers
  const handleIncrement = (productName) => {
    // clone
    let newProducts = [...products];
    // update
    let index = newProducts.findIndex((item) => item.name === productName);
    newProducts[index].count = newProducts[index].count + 1;
    // setState
    setProducts(newProducts);
  };

  const handleDecrement = (productName) => {
    // clone
    const newProducts = [...products];
    // update
    const index = newProducts.findIndex((p) => p.name === productName);
    if (newProducts[index].count > 0) newProducts[index].count = newProducts[index].count - 1;
    // setState
    setProducts(newProducts);
  };

  const handleReset = () => {
    // clone
    let newProducts = [...products];
    // update
    newProducts = newProducts.map((p) => {
      return { ...p, count: 0 };
    });
    // setState
    setProducts(newProducts);
  };

  const hadleAddToCart = (productId, count = 1) => {
    // clone
    let newProducts = [...products];
    // update
    let index = newProducts.findIndex((p) => p.id === productId);
    newProducts[index].isInCart = !newProducts[index].isInCart;
    newProducts[index].count = count;
    // setState
    setProducts(newProducts);
    toast.success("Item added to cart");
  };

  const hadleRemoveFromCart = (productId, count = 0) => {
    // clone
    let newProducts = [...products];
    // update
    let index = newProducts.findIndex((p) => p.id === productId);
    newProducts[index].isInCart = !newProducts[index].isInCart;
    newProducts[index].count = count;
    // setState
    setProducts(newProducts);
    toast.error("Item removed from cart");
  };

  const handleDelete = (productId) => {
    // clone
    let newProducts = [...products];
    // update
    let index = newProducts.findIndex((p) => p.id === productId);
    newProducts[index].isInCart = !newProducts[index].isInCart;
    // setState
    setProducts(newProducts);
    toast.error("Item Deleted");

    // let newProducts = [...products];
    // const index = newProducts.findIndex((p) => p.name === productName);
    // newProducts.splice(index, 1);
    // setProducts(newProducts);
  };
  return (
    <>
      <Router>
        <Header products={products.filter((p) => p.isInCart)} />
        <ToastContainer position="bottom-right" autoClose={1500} />
        <div className="container">
          <Routes>
            <Route
              path="/"
              element={
                <Home
                  products={products}
                  hadleAddToCart={hadleAddToCart}
                  handleIncrement={handleIncrement}
                  handleDecrement={handleDecrement}
                  hadleRemoveFromCart={hadleRemoveFromCart}
                />
              }
            />
            <Route
              path="/cart"
              element={
                <Cart
                  products={products.filter((p) => p.isInCart)}
                  handleIncrement={handleIncrement}
                  handleDecrement={handleDecrement}
                  handleReset={handleReset}
                  handleDelete={handleDelete}
                />
              }
            />
            <Route
              path="/menu"
              element={<Menu products={products} hadleAddToCart={hadleAddToCart} categories={categories} />}
            />
            <Route path="/about" element={<About />}>
              <Route path="team" element={<AboutTeam />} />
              <Route path="company" element={<AboutCompany />} />
            </Route>
            <Route path="/product/:name/:id" element={<ProductPage />} />
            <Route path="*" element={<NotFound />} />
          </Routes>
        </div>
      </Router>
    </>
  );
};

export default App;
