const Product = (props) => {
  const { name, price, count } = props.product;

  return (
    <div className="row p-2 d-flex justify-content-center">
      <span className="fw-bold col-3">{name}</span>
      <span className="col-2">{price} $</span>
      <div onClick={() => props.handleIncrement(name)} className="btn btn-primary col-1">
        +
      </div>
      <span className="col-1 d-flex justify-content-center align-items-center">{count}</span>
      <div onClick={() => props.handleDecrement(name)} className="btn btn-primary col-1">
        -
      </div>
      <div className="btn btn-danger btn-sm col-1 mx-3" onClick={() => props.handleDelete(name)}>
        <i className="fa-solid fa-trash-can"></i>
      </div>
    </div>
  );
};

export default Product;
