import { useLocation, useNavigate } from "react-router-dom";

import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { ButtonGroup } from "@mui/material";

const Home = ({ products, hadleAddToCart, handleIncrement, handleDecrement, hadleRemoveFromCart }) => {
  const location = useLocation();

  const navigate = useNavigate();

  const handleGoToFool = () => {
    // logic
    console.log("LOGIC Done!");
    navigate("/product/fool/22", { replace: true });
  };

  return (
    <>
      <div className="d-flex flex-wrap justify-content-center">
        {/* {products.map((p) => console.log(p.name + " = " + p.count))} */}

        {products.map((p) => (
          <Card sx={{ maxWidth: 210 }} style={{ margin: 10 }}>
            <CardMedia component="img" height="150" image={p.img} alt="green iguana" />
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {p.name}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {p.price}$
              </Typography>
            </CardContent>
            <CardActions>
              <ButtonGroup size="small" aria-label="small outlined button group">
                <Button onClick={() => handleIncrement(p.name)}>+</Button>
                <Button>{p.count}</Button>
                <Button onClick={() => handleDecrement(p.name)}>-</Button>
              </ButtonGroup>
              {p.isInCart == false ? (
                <Button size="small" onClick={() => hadleAddToCart(p.id, p.count > 1 ? p.count : 1)}>
                  <Typography variant="caption">Add to cart</Typography>
                </Button>
              ) : (
                <Button size="small" onClick={() => hadleRemoveFromCart(p.id)}>
                  <Typography variant="caption">
                    <span style={{ color: "red" }}>Remove from cart</span>
                  </Typography>
                </Button>
              )}
            </CardActions>
          </Card>
        ))}

        {/*         
        <div class="card border-info m-3" style={{ width: 200 }}>
          <div class="card-header">Header</div>
          <div class="card-body">
            <h4 class="card-title">Info card title</h4>
            <p class="card-text">
              Some quick example text to build on the card title and make up the bulk of the card's content.
            </p>
          </div>
        </div> */}
      </div>
      {/* <h1>Welcome to Burger King</h1>
      <div onClick={handleGoToFool} className="btn btn-danger">
        Go To Fool
      </div> */}
    </>
  );
};

export default Home;
